@import url(https://fonts.google.com/specimen/Roboto);
@import url(https://fonts.googleapis.com/css2?family=Sorts+Mill+Goudy&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

h1{font-size:65px;font-weight:500}h2{font-size:45px;font-weight:500}h3{font-size:32px;font-weight:500;line-height:40px}.app .intro{width:50%;max-width:600px;padding:50px}.app h2{line-height:70px;font-family:'Roboto', sans-serif}.top-navigation{padding:20px 50px;display:flex;flex-direction:row;justify-content:space-between}.top-navigation ul{align-items:center;width:300px;display:flex;flex-direction:row;justify-content:space-evenly}.top-navigation ul a{color:#28282a;font-family:'Sorts Mill Goudy', serif;border-bottom:2px solid transparent}.top-navigation ul a:hover{color:#5ccbc3}.top-navigation ul a.active{color:#5ccbc3;border-bottom:2px solid #5ccbc3}.top-navigation h3{color:#28282a;font-family:'Sorts Mill Goudy', serif}.display{height:550px;width:100%;margin-bottom:5px}.display img{width:100%;height:100%;object-fit:cover;object-position:40% 45%}.about{height:25%;display:flex;flex-direction:row;margin-bottom:5px}.about div{position:relative;width:50%}.about div:nth-of-type(2){padding-right:50px}.about div:nth-of-type(2) p{padding:50px;max-width:500px;font-size:25px;line-height:40px}.about div img{padding-left:50px;width:80%}.contact{box-sizing:border-box;height:480px;background:#5ccbc3;width:100%;padding:90px 50px;margin-bottom:5px}.contact p{margin:10px 0 30px 0;width:50%;max-width:600px;line-height:28px}.contact h2,.contact p{color:#fff}.contact h2{margin:30px 0}.contact p{font-size:16px}.contact button{cursor:pointer;font-size:16px;border:none;border-radius:0;padding:1.2em 2.004em}.contact button:hover{background:#28282a;color:white}.contact button a{color:inherit;text-decoration:inherit}.contact button:first-of-type{margin-right:20px}footer{background:#28282a;margin-top:5px}footer ul{width:180px;justify-content:space-between;padding:50px 0;display:flex;flex-direction:row;margin:0 auto}footer ul li{cursor:pointer;height:40px;width:40px;border-radius:50%;border:2px solid #5ccbc3;display:flex;justify-content:center;align-items:center}footer ul li img{-webkit-filter:invert(79%) sepia(76%) saturate(303%) hue-rotate(111deg) brightness(84%) contrast(88%);filter:invert(79%) sepia(76%) saturate(303%) hue-rotate(111deg) brightness(84%) contrast(88%);height:35px}footer ul .email{height:23px;margin-top:2px}footer ul .github{height:28px}

.work{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:50px 0}.work div{width:60%;padding-bottom:50px}.work .frontend{width:35%}.work .logos{display:flex;flex-direction:row;padding-top:30px;justify-content:space-around}.work .logos img{width:25%}.work .logos img:nth-of-type(3){width:35%}

h1{font-size:65px;font-weight:500}h2{font-size:45px;font-weight:500}h3{font-size:32px;font-weight:500;line-height:40px}.about{height:25%;display:flex;flex-direction:row;margin-bottom:5px}.about div{position:relative;width:50%}.about div:nth-of-type(2) p{padding:50px;max-width:500px;font-size:25px;line-height:40px}.about div img{padding-left:50px;width:80%}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;vertical-align:baseline;font-family:'Roboto', sans-serif;text-decoration:none}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

